// Инициализация Ktor Client для JS таргета
val client = HttpClient(Js) {
    install(ContentNegotiation) {
        json(Json {
            prettyPrint = true
            isLenient = true
            ignoreUnknownKeys = true
        })
    }
    install(Logging) {
        level = LogLevel.INFO
    }
}

// Функция для отправки сообщения с инлайн-кнопками
fun sendInlineButtonMessage() {
    val chatId = "409490616"
    val botToken = "7359727741:AAFV9nRRfLBQYlzHfxxZUCLyrBmMOZcH_ys"
    val url = "https://api.telegram.org/bot$botToken/sendMessage"

    val myBody = MyBody(
        chat_id = chatId,
        text = "Выберите одну из кнопок:",
        reply_markup = ReplyMarkup(
            inline_keyboard = listOf(
                listOf(
                    InlineTgItem(text = "Нажми меня", callback_data = "button_click"),
                    InlineTgItem(
                        text = "Открыть веб-приложение",
                        url = "https://t.me/fry_lab_bot/FryLab"
                    )
                )
            )
        )
    )

    MainScope().launch {
        try {
            val response = client.post(url) {
                contentType(ContentType.Application.Json)
                setBody(Json.encodeToString(myBody))
            }
            window.alert("Сообщение успешно отправлено: ${response.body<String>()}")
        } catch (e: Exception) {
            window.alert("Ошибка при отправке сообщения: ${e.message}")
        }
    }
}

@Serializable
data class MyBody(
    val chat_id: String,
    val text: String,
    val reply_markup: ReplyMarkup
)

@Serializable
data class ReplyMarkup(
    val inline_keyboard: List<List<InlineTgItem>>
)

@Serializable
data class InlineTgItem(
    val text: String,
    val callback_data: String? = null,
    val url: String? = null,
    val web_app: String? = null
)

@Serializable
data class Command(
    val command: String,
    val description: String
//    val commands: List<InlineTgItem>
)

private fun setInitButton() {
    val botToken = "7359727741:AAFV9nRRfLBQYlzHfxxZUCLyrBmMOZcH_ys"
    val url = "https://api.telegram.org/bot$botToken/setMyCommands"

    val commands = listOf(
        Command(command = "open_app", description = "Open the mini app")
    )

    val button = InlineTgItem(
        text = "Open mApp",
        web_app = "https://t.me/fry_lab_bot/FryLab"
    )

    MainScope().launch {
        try {
            val response = client.post(url) {
                contentType(ContentType.Application.Json)
                setBody(Json.encodeToString(commands))
            }
            window.alert("Кнопка успешно добавлена: ${response.body<String>()}")
        } catch (e: Exception) {
            window.alert("Ошибка при добавлении кнопки: ${e.message}")
        }
    }
}